.quizMain{
    display:flex;
   
    margin-top: 70px;
    overflow-y: hidden;
    
    
}

.quizCard {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    height: 500px;
    overflow: hidden;
    background-color: #ecf0f1;
    position: relative;
    
    
}

.quizBtn {
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;
}

.quizTimer {
    align-self: flex-end;
    font-size: 30px;
    font-weight: bold;
}


.quiz {
  display: grid;
  grid-template-rows: 60px auto;
  width: 800px;
  height: 500px;
  background-color: #ecf0f1;
  box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
}
.quiz_body {
  padding: 30px 30px;
}
.option_group {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    list-style-type: none;
    
}
.option {
  display: block;
  width: 300px;
  background-color: #fff;
  margin-bottom: 20px;
  padding: 15px 20px;
  border-radius: 100px;
  border: 2px solid transparent;
  transition: 0.4s all;
  margin-left: 30px;
}

.quizRadioBtns {
    display: block;
  width: 300px;
  background-color: red;
  margin-bottom: 20px;
  padding: 15px 20px;
  border-radius: 100px;
  border: 2px solid transparent;
  /* transition: 0.4s all; */
  margin-left: 30px;
}

.option input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    width: 300px;
}

.option:hover {
  cursor: pointer;
  border: 2px solid #341f97;
  color: #341f97;
}
.isactive 
{
  background-color: #341f97;
  color: #fff;
}

.isactive:hover {
    cursor: pointer;
  border: 2px solid #fff;
  color: #fff;
}


.radiocontainer {
    /* background-color: #f1f1f1; */
    display: block;
    position: relative;
    padding: 10px 10px 10px 50px;
    margin-bottom: 1px;
    cursor: pointer;
    font-size: 18px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    word-wrap: break-word;
}

.checkmark {
    position: absolute;
    top: 15px;
    left: 15px;
    height: 19px;
    width: 19px;
    background-color: #fff;
    border-radius: 50%;
}

#altcontainer {
    background-color: #fff;
    font-size: 120%;
    line-height: 1.7em;
}


.radiocontainer input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

input[type="radio" i] {
    background-color: initial;
    cursor: default;
    appearance: radio;
    box-sizing: border-box;
    margin: 3px 3px 0px 5px;
    padding: initial;
    border: initial;
}


